                            show: false
                        },
                        axisTick: {
                            show: false
                        },
                        splitLine: {
                            show: false
                        },
                        axisPointer: {
                            show: false
                        },
                        data: data.y
                    },
                    zAxis3D: {
                        show: false,
                        max: data.boxHeight,
                        axisLabel: {
